@use '../tools' as *;

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  height: 100%;

  font-size: 62.5%;
  line-height: $main-line-height;
  color: $default-color;
}

body {
  position: relative;

  min-width: $phone-portrait-min-width;
  padding: 0;

  font-family: $main-font-family;
  font-weight: 400;
  font-size: $main-font-size;
  line-height: 1.2;
  color: $default-color;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4 {
  line-height: 1.25;
}

a {
  &,
  &:hover {
    text-decoration: none;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.hidden {
  display: none !important;

  &_mobile {
    @media (max-width: $phone-landscape-max-width) {
      display: none !important;
    }
  }

  &_mobile-tablet {
    @media (max-width: $tablet-portrait-max-width) {
      display: none !important;
    }
  }

  &_tablet {
    @media (min-width: $tablet-min-width) and (max-width: $tablet-portrait-max-width) {
      display: none !important;
    }
  }

  &_desktop {
    @media (min-width: $s-desktop-min-width) {
      display: none !important;
    }
  }

  &_desktop-tablet {
    @media (min-width: $tablet-min-width) {
      display: none !important;
    }
  }
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  border: 0 !important;
  clip: rect(0, 0, 0, 0) !important;
  -webkit-clip-path: inset(100%) !important;
  clip-path: inset(100%) !important;
}
